.main-action {
  width: 48.5%;
  height: 462px;

  background-color: $red;
  background-image: url("../img/action-block.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right;
}

.main-action__year {
  font-family: $RobotoCondensed;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 44px;
  color: $white;
  margin: 0;
  margin-left: 46px;
  margin-top: 33px;
}

.main-action__price {
  font-family: $RobotoCondensed;
  font-style: normal;
  font-weight: bold;
  font-size: 70px;
  color: $white;
  margin: 0;
  margin-top: -13px;
  margin-left: 46px;
}

.main-action__description {
  width: 189px;
  font-family: $RobotoCondensed;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 25px;
  color: $white;
  margin: 0;
  margin-top: 13px;
  margin-left: 46px;
}

.main-action__link {
  display: block;
  font-family: $RobotoCondensed;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  color: $white;
  margin-top: 189px;
  margin-left: 46px;

  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 78px;
    height: 1px;
    top: 16px;
    left: 0;
    background-color: $white;
  }

  &:hover,
  &:focus {
    color: $dark-blue;
    outline: none;
    transition: 0.3s;
  }

  &:hover::before,
  &:focus::before {
    display: none;
  }

  &:active {
    opacity: 0.7;
  }
}

@media (max-width: $tablet) {
  .main-action {
    width: 100%;
    height: 462px;

    background-color: $red;
    background-image: url("../img/action-block-tablet.jpg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right;
  }
}

@media (max-width: $mobile) {
  .main-action {
    width: 100%;
    height: 598px;
    background-image: url("../img/action-block-mobile.jpg");
    background-size: 100% 299px;
    background-position: bottom;
  }

  .main-action__year {
    width: 41px;
    margin-left: auto;
    margin-right: auto;
  }

  .main-action__price {
    width: 142px;
    margin-left: auto;
    margin-right: auto;
  }

  .main-action__description {
    width: 189px;
    text-align: center;
    margin-top: 7px;
    margin-left: auto;
    margin-right: auto;
  }

  .main-action__link {
    width: 79px;
    display: block;
    font-family: $RobotoCondensed;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    text-decoration: none;
    color: $white;
    margin-top: 31px;
    margin-left: auto;
    margin-right: auto;
  }
}
